import {reqGoodsInfo,reqAddOrUpdataShopCart} from '@/api';
//封装游客身份模块uuid-->生成一个随机字符串，不能变的
import {getUUID} from '@/utils/uuid_token';
const state = {
    goodInfo:{},
    //游客的临时身份
    uuid_token:getUUID(),
};
const mutations = {
    GETGOODINFO(state,goodInfo){
        state.goodInfo = goodInfo;
    }
};
const actions = {
    //获取产品信息的actions
    async getGoodInfo({commit},skuId){
        let result = await reqGoodsInfo(skuId);
        if(result.code == 200){
            commit('GETGOODINFO',result.data);
        }
    },
    //将产品添加到购物车
    async addOrUpdataShopCart({commit},{skuId,skuNum}){
        //加入购物车返回的结构
        //加入购物车发请求以后，前台将数据带给服务器
        //服务器写入数据成功，并没有返回其他值，只是返回code=200， 代表这次成功
        //因为服务器没有返回其他值，因此不需要vuex三连环存储数据
        let result = await reqAddOrUpdataShopCart(skuId,skuNum);
        //当前的这个函数如果执行返回的promise
        //result.code == 200代表加入购物车成功
        if(result.code == 200){
            return 'ok';
        }else{
            //代表加入购物车失败
            return Promise.reject(new Error('fail'));
        }
    },
};
const getters = {
    //路径导航的数据
    categoryView(state){
        return state.goodInfo.categoryView||{};
    },
    //产品信息的数据
    skuInfo(state){
        return state.goodInfo.skuInfo||{};
    },
    //产品售卖属性的数据
    spuSaleAttrList(state){
        return state.goodInfo.spuSaleAttrList||{};
    }

};
export default{
    state,
    mutations,
    actions,
    getters,
}
