export default[
    {
        path:"/home",
        component:()=>import('@/pages/Home'),//这种写法可以实现路由懒加载
        meta:{show:true}
    },
    {
        //下面这种写法，代表params参数可以进行传递，也可以不穿，？（正则：两次代表出现次数0|1）
        path:"/search/:keyword?",
        component:()=>import('@/pages/Search'),
        meta:{show:true},
        name:"search",
        //路由可以给组件传递props，函数的写法才是重要的
        props:(route)=>({
            keyword:route.params.keyword,
            big:route.query.big,
        }),
    },
    {
        path:"/login",
        component:()=>import('@/pages/Login'),
        meta:{show:false}
    },
    {
        path:"/register",
        component:()=>import('@/pages/Register'),
        meta:{show:false}
    },
    {
        path:"/detail/:skuid",
        component:()=>import('@/pages/Detail'),
        meta:{show:false},
        name:"detail"
    },
    {
        path:"/addcartsuccess",
        component:()=>import('@/pages/AddCartSuccess'),
        meta:{show:true},
        name:"addcartsuccess"
    },
    {
        path:"/shopcart",
        component:()=>import('@/pages/ShopCart'),
        meta:{show:true},
        name:"shopcart"
    },
    {
        path:"/trade",
        component:()=>import('@/pages/Trade'),
        meta:{show:true},
        name:"trade"
    },
    {
        path:"/pay",
        component:()=>import('@/pages/Pay'),
        meta:{show:true},
        name:"pay"
    },
    {
        path:"/paysuccess",
        component:()=>import('@/pages/PaySuccess'),
        meta:{show:true},
        name:"paysuccess"
    },
    {
        path:"/center",
        component:()=>import('@/pages/Center'),
        meta:{show:true},
        name:"center"
    },
    //重定向，在项目跑起来的时候，访问/，立马让他定向到首页
    {
        path:'*',
        redirect:"/home"
    }
]