//配置路由
import Vue from 'vue';
import VueRouter from 'vue-router';

//使用插件
Vue.use(VueRouter);

import store from '../store';
import routes from './routes';

//重写push|replace
// 第一个参数：告诉原来的push方法，你往哪里跳转（传递哪些参数）
// 第二个参数：成功回调
// 第三个参数：失败回调
// call和apply的区别
// 相同点：都可以调用函数一次，都可以篡改上下文一次
// 不同点：call和apply传递参数：call传递参数用都号隔开，apply方法执行，传递数组
let originPush = VueRouter.prototype.push;
let originReplace = VueRouter.prototype.replace;
VueRouter.prototype.push = function (location,resolve,reject){
    if(resolve&&reject){
        originPush.call(this,location,resolve,reject);
    }else{
        originPush.call(this,location,()=>{},()=>{});
    }
}
VueRouter.prototype.replace = function (location,resolve,reject){
    if(resolve&&reject){
        originReplace.call(this,location,resolve,reject);
    }else{
        originReplace.call(this,location,()=>{},()=>{});
    }
}

let router = new VueRouter({
    //配置路由
    routes,
    //跳转页面之后自动滚动到页面顶端
    scrollBehavior (to,from,savedPosition){
        return {x:0,y:0}
    }
});

//全局守卫，前置守卫
router.beforeEach(async(to,from,next)=>{
    //to：可以获取到你要跳转的那个路由信息
    //from：可以获取到你从哪个路由而来的信息
    //next：next()放行，next(path)放行到指定路径
    // next();
    let token = store.state.user.token;
    let name = store.state.user.userInfo.name;
    if(name){
        if(to.path == '/login'){
            next('/home');
        }else{
            if(name){
                next();
            }else{
                //本来只有首页header登录后显示用户名，跳转到其他页面，没有派发action，没有用户信息，
	//不会显示用户名，这样操作就可以在跳转之后其他页面也会显示用户名
                //没有用户信息，派发action让仓库存储用户信息在跳转
                try {
                    //获取用户信息
                    await store.dispatch('getUserInfo');
                    //放行
                    next();
                } catch (error) {
                    //token失效获取不到用户信息，重新登陆
                    await store.dispatch('userLogOut');
                    next('/login');
                }
            }
        }
    }else{
        next();
    }
})

//配置路由
export default router;